.graph-node {
  cursor: pointer;
}
#graph {
  min-width: 500px;
  width: 70%;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  border-radius: 0 0 1em 0;
  /* background: black; */
}
#graph svg {
  margin: 0
}
#preview, #connections {
  z-index: 10;
  width: 330px;
  overflow-y: auto;
  padding: 10px;
  text-align: center;
  background: rgba(123, 209, 113, 0.7);
  border-radius: 4px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 20px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  color: #333;
}
#preview {
  cursor: pointer;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
#preview:hover {
  background: rgba(123, 209, 113, 0.9);
}
#title-panel, #preview-panel {
  position: absolute;
  z-index: 10;
  top: 5%;
  right: 20px;
  width: 350px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
#title-div, #instructions {
  background: #7AD171;
  border-radius: 4px;
  color: #333;
  padding: 10px;
  text-align: center;
  margin-bottom: 20px;
}
#instructions {
  font-size: calc(0.9em + .5vh);
  line-height: 1.3em;
  padding-bottom: 40px;
  text-align: left;
}
#instructions ul {
  list-style-type: none;
  text-align: center;
  margin: 0;
  padding: 0;
}
#preview img {
  margin: auto;
  border-radius: 2px;
  max-height: 200px;
  max-width: 300px;
  margin-bottom: 10px;
}
#preview .typetag {
  margin: auto;
  text-align: center;
  padding: 0.2em 1em 0.2em 1em;
  border-radius: 10px;
  background: #0c7218;
  color: #F6EDD9;
  font-weight: bold;
  /* box-shadow: 2px 2px rgba(0, 74, 28, 0.2); */
  margin-bottom: 20px;
  margin-top: 20px;
}
main {
  padding-top: 5em;
  padding-right: 200px;
  padding-left: 5em;
  padding-bottom: 5em;
  background: rgba(240, 240, 240, 0.9);
  /* overflow-y: auto; */
  min-width: 200px;
  width: 100%;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  position: absolute;
  top: 0;
  left: 0;
  overflow-y: scroll;
  height: 100%;
  margin-bottom: 0;
  z-index: 20;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  /* flex-grow: 1; */
}
.fade-leave-active {
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.fade-enter-active {
  -webkit-transition: 0.4s;
  transition: 0.4s;
  -webkit-transition-delay: 0.4s;
          transition-delay: 0.4s;
}
.fade-enter {
  opacity: 0.0;
}
.fade-leave-to {
  opacity: 0;
}
.slide-leave-active,
.slide-enter-active {
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.slide-enter {
  opacity: 0.0;
}
.slide-leave-to {
  opacity: 0;
}
svg .not-focused {
  opacity: 0.2;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
svg {
  cursor: url('http://ssl.gstatic.com/ui/v1/icons/mail/images/2/openhand.cur'), move;
  /* background: rgba(0, 0, 0, 0.1); */
}
svg.zooming {
    cursor: url('http://ssl.gstatic.com/ui/v1/icons/mail/images/2/closedhand.cur'), move;
    cursor: -webkit-grabbing;
}
.preview-text p {
  display: block;
  line-height: 1.3em;
  font-size: 1.2em;
  text-align: left;
  width: 300px;
  margin: auto;
  margin-bottom: 10px;
}
h1, h2 {
  line-height: 1.4em;
  margin-left: 10px;
  margin-right: 10px;
}
.preview-text p.read-more {
  font-size: 0.9em;
  color: black;
  font-weight: 100;
  text-align: center;
}
#closeButton {
  z-index: 50;
  color: gray;
  position: absolute;
  top: 50px;
  right: 50px;
  font-size: 3em;
  cursor: pointer;
  font-variant: small-caps;
  font-family: Helvetica;
}
#closeButton:hover {
  color: black;
  /* text-decoration: underline; */
}
.invisible-work, .not-focused.invisible-work {
  display: hidden;
  opacity: 0;
  cursor: inherit;
}
#preview-panel {
  height: calc(94vh);
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
#connections {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  height: 100%;
}
#connections ul {
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  /* margin: auto; */
  margin: 0;
  padding: 0;
  overflow-y: scroll;
}
#connections li {
  display: block;
  padding: 7px;
  border-radius: 4px;
  border: dashed 1px rgb(48, 47, 47);
  margin-bottom: 10px;
  cursor: pointer;
  margin-left: 5px;
  margin-right: 5px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  font-size: 0.8em;
}
#connections li:hover {
  background: rgb(11, 182, 25);
  color: black;
}
#connections li.Work {
  font-style: italic;
}
#showInstr {
  position: absolute;
  left: 5vh;
  top: 5vh;
}
#zoomButtons {
  position: absolute;
  left: 5vh;
  bottom: 5vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#showInstr, #zoomIn, #zoomOut, #refreshButton {
  background: rgba(0, 0, 0, 0.5);
  color: rgba(240, 240, 240, 0.5);
  border-radius: 6px;
  padding: 5px;
  cursor: pointer;
}
#showInstr:hover, #zoomIn:hover, #zoomOut:hover, #refreshButton:hover {
  background: rgba(0, 0, 0, 0.8);
  color: rgba(240, 240, 240, 0.8);
}
#showInstr:active, #zoomIn:hover, #zoomOut:hover, #refreshButton:hover {
  background: black;
}
#zoomIn, #zoomOut, #refreshButton {
  width: 20px;
  height: 20px;
  margin-right: 5px;
  text-align: center;
  border-radius: 20px;
}
#ackButton {
  background: rgba(0, 0, 0, 0.5);
  color: #f6EDD9;
  padding: 5px;
  border-radius: 5px;
  text-align: center;
  cursor: pointer;
  bottom: 5vh;
  right: 5vh;
  position: absolute;
  opacity: 0.7;
  width: 200px;
}
#ackButton:hover {
  opacity: 1;
}
#instructions a {
  color: blue;
}
#instructions a:active {
  color: darkblue;
}

